const travelList = (list, fn) => {
  let x = list;
  while (x !== null) {
    fn(x);
    x = x.next;
    index += 1;
  }
};
const getIndex = (list, node) => {
  let x = list;
  let index = 0;
  while (x !== node) {
    x = x.next;
    index += 1;
  }
  return index;
};
